
body {
  nav {
    padding: 0 4em 0px 0px;
    ul{
      text-align: right;
    }
    ul,ul li{
      list-style-type: none;
      margin: 0px;
      padding: 0px;
      }
    ul li{
      display: inline-block;
      padding-left: 10px;
    }
    ul li:first-of-type{
      padding-left: 0px;
    }
  }
}

/* Touch */
body.is-touch {
    nav{
    }
    nav ul{
      width: 100px;
    }
    nav:hover ul{
      background-color: $color-bg;
    }
    nav:hover ul::before{
			color: $color-fg-bold;
      border-color: $color-fg-bold;
    }
    nav ul::before{
			content: '\f0c9';
      font-size: 20px;
		  @include icon;
      border-radius: 100%;
      color: $color-accent2-fg;
      line-height: 40px;
      text-align: center;
      width: 40px;
      margin: 20px 20px 0px 0px;
      padding: 0px;
      display: inline-block;
      border: 1px solid $color-accent2-fg;
    }

    nav:hover ul li{
      background-color: $color-bg;
    }
    nav:hover ul li{
      display: block;
      padding: 5px 10px;
      border-left: 4px solid $color-accent-border2;
      border-right: 4px solid $color-accent-border2;
    }
    nav ul li:first-of-type{
      margin-top: 10px;
      padding-top: 10px;
      border-top: 4px solid $color-accent-border2;
    }
    nav ul li:last-of-type{
      padding-bottom: 10px;
      border-bottom: 4px solid $color-accent-border2;
    }
    nav ul,
    nav ul li{
      list-style-type: none;
    }
    nav ul li{
      display: none;
    }
}
